$(function () {
    function handleFormSubmission(formId, newElementId, successMessage) {
        $(formId).on('click', 'button[type="submit"]', {
            passive: true
        }, function (event) {
            event.preventDefault();

            var newValue = $(newElementId).val();

            $.ajax({
                url: 'includes/post_settings.php',
                method: 'POST',
                data: {
                    formId: formId,
                    newValue: newValue
                },
                success: function (response) {
                    $(formId)[0].reset();
                    $(formId).find('.form-control').removeClass('is-invalid');
                    $(formId).find('.invalid-feedback').hide();
                    $('.success-message').text(successMessage);
                    $('.toast').toast('show');
                    $(formId).closest('.modal').modal('hide');
                    setTimeout(function () {
                        location.reload();
                    }, 1500);
                },
                error: function () {
                    alert('An error occurred while updating the setting.');
                }
            });
        });
    }

    handleFormSubmission('#editUsernameForm', '#newUsername', 'Username updated successfully.');
    handleFormSubmission('#editNameForm', '#newName', 'Name updated successfully.');
    handleFormSubmission('#editAvatarForm', '#newAvatar', 'Avatar updated successfully.');
    handleFormSubmission('#editPasswordForm', '#newPassword', 'Password updated successfully.');
    handleFormSubmission('#editConfigurationForm', '#enableNotifications', 'Notification Configuration updated successfully.');
    handleFormSubmission('#editTabConfigurationForm', '#enableTab', 'Theme Tab Configuration updated successfully.');
    handleFormSubmission('#editEndpointForm', '#newEndpoint', 'Endpoint updated successfully.');
    handleFormSubmission('#editCSSForm', '#newCSS', 'Custom CSS updated successfully.');
    handleFormSubmission('#editJSForm', '#newJS', 'Custom JavaScript updated successfully.');
    handleFormSubmission('#resetModulesForm', '#confirmPassword', 'Modules reset successfully.');
});